import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Global')
export class Global extends Component {

	@property({type: Number}) 
    winCount = 0;

    @property({type: Number}) 
    totalPlayed = 0;

    @property({type: Number}) 
    entires = 0;

    @property({type: Number}) 
    entireLevel = 0;

    @property({type: Number}) 
    ways = 0;

    @property({type: Number}) 
    coinValue = 0;

    @property({type: Number}) 
    totalWinPrice = 0;

    @property({type: Number}) 
    soundStatus = 0;

}

